@mixin desktop {
  @media(max-width:1320px){
      @content
  }
}
@mixin pad {
  @media(max-width:992px){
      @content
  }
}
@mixin mobile {
  @media(max-width:767px){
      @content
  }
}
@mixin smobile {
  @media(max-width:576px){
      @content
  }
}
@mixin minmobile {
  @media(max-width:320px){
      @content
  }
}
